// Variables

// Root
:root {
  --primary: 			#3827b0;
  --primary-light: 		#c3bee7;
  --light: 				#f5f5f5;
  --light-light: 		#eee;
  --grey: 				#bdbdbd;
  --grey-darker: 		#b1b1b1;
  --white: 				#fff;
  --black: 				#000;
}

// Text Alignment
$center:    			center !default;

// Fonts
$font-weight-semibold:  500 !default;
$font-weight-bold:		700 !default;

.dtp {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.38);
  z-index: 2000;
  font-size: .938rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  .btn {
    border-radius: .25rem;
    min-width: 0;
  }

  > .dtp-content {
    background: var(--white);
    border-radius: .25rem;
    display: flex;
	flex-direction: column;
    width: 330px;
    -webkit-box-shadow: 0 10px 15px -3px rgba(0,0,0,.07),0 4px 6px -2px rgba(0,0,0,.05);
    box-shadow: 0 10px 15px -3px rgba(0,0,0,.07),0 4px 6px -2px rgba(0,0,0,.05);
    position: fixed;
    left: 50%;
    top: 50%;
    max-height: calc(100vh - 32px);
    overflow-y: auto;
    -webkit-animation: pickerFadeIn 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    animation: pickerFadeIn 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    margin-bottom: 1rem;
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: center bottom 0px;
    transform-origin: center bottom 0px;
    z-index: 1066;
  }

  .dtp-date-view .dtp-header {
    background: var(--primary);
    color: var(--white);
    text-align: $center;
    padding: .25rem 1.5rem;
    display: flex;
    flex-direction: column;
  }

  .dtp-date, .dtp-time {
    background: var(--primary);
    text-align: $center;
    color: var(--white);
    padding: .625rem;
  }

  .dtp-date > div {
    padding: 0;
    margin: 0;
  }

  .dtp-actual-month {
    font-size: 1.5rem;
  }

  .dtp-actual-num, .dtp-actual-maxtime {
    font-size: 3rem;
    line-height: 0.9;
  }

  .dtp-actual-year {
    font-size: 1.5rem;
    color: var(--primary-light);
    -webkit-transition: color 0.3s;
    transition: color 0.3s;

    &:hover {
      color: var(--white);
      cursor: pointer;
    }

    &.disabled {
      color: var(--primary-light);
      cursor: initial;
    }
  }

  .dtp-picker {
    position: relative;
	height: 100%;
  }
  
  .dtp-view {
	padding-left: 12px;
	padding-right: 12px;
	outline: none;
  }

  .dtp-picker-month, .dtp-actual-time {
    font-weight: 500;
    text-align: $center;
  }

  .dtp-picker-month {
    padding: .625rem .75rem 0;
	display: flex;
	justify-content: center;
  }

  table.dtp-picker-days {
    margin-right: auto;
    margin-left: auto;
    width: 304px;

    tr {
      border: none;

      > td {
        border: none;
        font-weight: 500;
        font-size: .75rem;
        text-align: $center;
        padding: .5rem .25rem;
        line-height: 1.428572;

        > a {
          color: rgba(0, 0, 0, 0.87);
          text-decoration: none;
          padding: .688rem .688rem .688rem .75rem;
          border-radius: 50%;
        }
      }
    }
  }

  .dtp-picker-time > a {
    color: rgba(0, 0, 0, 0.87);
    text-decoration: none;
    padding: .688rem .688rem .688rem .75rem;
    border-radius: 50%;
  }

  table.dtp-picker-days tr > {
    td > a.selected {
      background: var(--primary);
      color: var(--white);
    }

    th {
      color: var(--grey-darker);
      text-align: $center;
      font-weight: $font-weight-bold;
      padding: .375rem .25rem;
      text-transform: uppercase;
    }
  }

  .p10 {
    > a {
      color: var(--light);
      text-decoration: none;
    }

    width: 10%;
    display: inline-block;
  }

  .p20 {
    width: 20%;
    display: inline-block;
  }

  .p60 {
    width: 60%;
    display: inline-block;
  }

  .p80 {
    width: 80%;
    display: inline-block;
  }

  a {
    &.dtp-meridien-am, &.dtp-meridien-pm {
      position: relative;
      top: .625rem;
      color: rgba(0, 0, 0, 0.87);
      font-weight: $font-weight-semibold;
      padding: .688rem .563rem;
      border-radius: 50%;
      text-decoration: none;
      background: var(--light-light);
      font-size: 1rem;
    }
  }

  .dtp-actual-meridien a.selected {
    background: var(--primary);
    color: var(--white);
  }

  .dtp-picker-time > {
    .dtp-select-hour, .dtp-select-minute {
      cursor: pointer;
    }
  }

  .dtp-buttons {
    padding: 1rem;
    text-align: right;
  }

  &.hidden, .hidden {
    display: none;
  }

  .invisible {
    visibility: hidden;
  }

  .left {
    float: left;
  }

  .right {
    float: right;
  }

  .clearfix {
    clear: both;
  }

  .center {
    text-align: $center;
  }
}

@-webkit-keyframes pickerFadeIn {
  from {
    opacity: 0;
    -webkit-transform: scale(0.8);
  }

  to {
    opacity: 1;
    -webkit-transform: scale(1);
  }
}

@keyframes pickerFadeIn {
  from {
    opacity: 0;
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }

  to {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.dtp-year-picker {
  display: none;
  overflow: auto;
  margin-bottom: .625rem;
  max-height: 466px;
}

.dtp-buttons {
  overflow: hidden;
  min-height: 50px;
  display: flex;
  position: relative;
  width: 100%;
  bottom: 0;
  justify-content: flex-end;
  align-items: center;
}

.year-picker-item {
  text-align: $center;
  padding: 0.4rem 0;
  font-size: 1.3rem;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;

  &:hover {
    cursor: pointer;
    color: var(--primary);
  }

  &.active {
    color: var(--primary);
    font-size: 2rem;
    font-weight: $font-weight-bold;
  }
}

// enabled day

a.dtp-select-day {
  -webkit-transition: background-color 100ms linear;
  transition: background-color 100ms linear;

  &:hover {
    background-color: rgba(var(--primary-light), 0.6);
  }
}

// disabled day

span.dtp-select-day {
  color: var(--grey);
  padding: 0.7em 0.7em 0.7em 0.8em;
}

circle.dtp-select-hour *:hover {
  fill: var(--primary-light);
}

.dtp {
  .table tbody {
    td, th {
      height: 2.5rem;
    }
  }

  .btn {
    border: 0 !important;
    border-radius: .25rem;
    -webkit-box-shadow: none;
    box-shadow: none;
    font-size: .8125rem;
    font-weight: 500;
    line-height: 1;
    padding: .59375rem 1rem;
    min-width: 0;
    margin: 0;
    margin-left: .25rem;
    text-transform: uppercase;
  }
}

.table:not(.table-bordered) {
  thead th, td, th {
    border: 0 !important;
  }
}